.main-chart, .chart-options {
  flex-grow: 1;
  padding: 16px;
  margin: 16px auto;
}

.main-chart {
	background-color: #00ff00;
  min-height: 500px;
}

.chart-options {
  border-radius: 8px;
  border: 1px solid #bdbdbd;
}

.graph-count {
  font-weight: bold;
}

.graph-bar {
  background-color: red;
  transition: 0.5s;
}

.graph-title {
  text-align: center;
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 32px;
}

.form-item {
  margin-bottom: 4px;
}

.form-item input:first-of-type {
  margin-right: 4px;
}

.graph-info {
  margin-bottom: 4px;
}

.graph-item:not(:last-child) {
  margin-bottom: 16px;
}

.title-form {
  margin-bottom: 16px;
}

.title-form input:not(:last-of-type) {
  margin-right: 4px;
}

button:not(:last-of-type) {
  margin-right: 8px;
}
